import React, { Component } from 'react';
import './ExamCard.css';

class ExamCard extends Component {
  render () {
    return (
      <div className="carousel-item card exam-card">
        <div className="card-title">Data Structures</div>
        <p>
            Learn Data Structures by giving this test and checking your scores.
        </p>
      </div>
    );
  }
}

export default ExamCard;
